<template>
<nav class="side-menu">
    <div>
        <div class="nav js_nav auto-height" v-if="!menuSwitch" :class="{ active: menuSwitch }">
            <div @click="toggleMenu()" class="nav-top js_nav_switch"></div>
            <div v-for="menu in menus" :key='menu.menuId' class="nav-box js_nav_box">
                <div :class='"openStatus_" + menu.menuId' class="nav-icon"></div>
                <dl>
                    <dt @click="toggleSubmenu(menu)" class="js_nav_1">
                        <span>{{menu.text}}</span>
                        <i class="icon icon-down02 fr mgr-15"></i>
                    </dt>
                    <ul v-show="menu.show" class="nav">
                        <li :class="{'active': selected === submenu.menuId}" v-for="submenu in menu.submenu"  :key='submenu.menuId' @click="clickMenuItem(submenu)"><a>{{submenu.text}}</a></li>
                    </ul>
                </dl>
            </div>
        </div>
    </div>
</nav>
</template>
<script>
import  $ from 'jquery'
import Vue from 'vue'

export default {
    name: 'sideMenu',
    data () {
        return {
            menus: [],
            selected: '管理',
            menuSwitch: false,
            showSubmenu: false
        }
    },
    created () {
        var  _this = this;
                
        this.$taber.$on('vue-tabs-active-change', (tab) => {
            if (tab) {
                this.selected = tab.meta.menuId
            } else {
                this.selected = null
            }
        })

        $.getJSON('../static/getMenu.copy.json', function(json) {
            var res = json.root.response_body;

            _.forEach(res, function (item, index) {
                if (index === 0) {
                    item['show'] = true;
                } else {
                    item['show'] = false;
                }
                _this.menus.push(item);
                
            });

            _this.$taber.open({
                name: 'home'
            })
        });
    },
    methods: {
        toggleMenu: function () {
            this.menuSwitch = !this.menuSwitch;
        },
        toggleSubmenu: function (item) {
            Vue.set(item, 'show', this.show = !this.show);
            if (item.show) {
                _.forEach(this.menus, function (temp) {
                    if (temp.menuId !== item.menuId) {
                        Vue.set(temp, 'show', false);
                    }
                });
            }

        },
        clickMenuItem (item) {
            this.selected = item.text
            
            this.$taber.open({
                name: item.sref,
                params: {
                    title: item.text
                }
            })
        }
    }
}
</script>
<style lang="less">
.side-menu {
    position: fixed;
    width: 200px;
    height: 100%;
    left: 0px;
    top: 1px;
    background-color: #2f4050;
    color: #a7b1c2;

    .nav {
        list-style: none;
        padding: 0px;

        > li.active {
            color: #fff;
        }

        > li > a {
            padding: 4px 10px 4px 40px;
            display: block;
            text-align: left;

            &:hover {
                cursor: pointer;
                color: #fff;
            }
        }
    }
}
</style>


