<template>
    <transition name="slide">
        <div>
            <h3> 用户管理 {{myMessage}}</h3>
            <input type="text" v-model="aaa" name="name" />
            <button @click="showInfo()">打印vModel</button>
            <div>
                <mu-raised-button label="测试对话框" @click="open"/>
                <mu-dialog :open="dialog" @close="close" title="Scrollable Dialog" scrollable>
                    <mu-raised-button label="unselect" @click="unselect"/>
                        <mu-table multiSelectable enableSelectAll ref="table">
                            <mu-thead>
                            <mu-tr>
                                <mu-th>ID</mu-th>
                                <mu-th>Name</mu-th>
                                <mu-th>Status</mu-th>
                            </mu-tr>
                            </mu-thead>
                            <mu-tbody>
                            <mu-tr>
                                <mu-td>1</mu-td>
                                <mu-td>John Smith</mu-td>
                                <mu-td>Employed</mu-td>
                            </mu-tr>
                            <mu-tr>
                                <mu-td>2</mu-td>
                                <mu-td>Randal White</mu-td>
                                <mu-td>Unemployed</mu-td>
                            </mu-tr>
                            <mu-tr>
                                <mu-td>3</mu-td>
                                <mu-td>Stephanie Sanders</mu-td>
                                <mu-td>Employed</mu-td>
                            </mu-tr>
                            <mu-tr>
                                <mu-td>4</mu-td>
                                <mu-td>Steve Brown</mu-td>
                                <mu-td>Employed</mu-td>
                            </mu-tr>
                            </mu-tbody>
                        </mu-table>
                    <div class="bottom">
                        <mu-raised-button label="取消" @click="close" color="#fff" backgroundColor="#0184ce"/>
                        <mu-raised-button label="确定" @click="close" color="#fff" backgroundColor="#23b989"/>
                    </div>
                </mu-dialog>
            </div>
        </div>
    </transition>
</template>

<script>
import Vue from "vue"
import VuejsDialog from "vuejs-dialog"

Vue.use(VuejsDialog);

export default {
    props: ['myMessage'],
    methods: {
        showInfo: function () {
            this.$emit('update:myMessage', this.aaa);
            // var aaa = {
            //         blncUplimit: '',
            //         calType: ''
            //     },
            //     param = {
            //         jsonparam: aaa
            //     };

            // axios.post('../../iease/scene/save_base', param)
            //     .then(function (response) {
            //         console.log(response);
            // });

            this.$dialog.confirm('Please confirm to continue')
                .then(function () {
                    console.log('Clicked on proceed');
                })
                .catch(function () {
                    console.log('Clicked on cancel')
                });
        },
        open () {
            this.dialog = true
        },
        close () {
            this.dialog = false
        },
        unselect () {
            this.$refs.table.unSelectAll()
        }
    },
    data: function () {
        return {
            dialog: false,
            aaa: this.myMessage
        }
    }
}
</script>

<style scoped>

</style>



